var that;
export default {
	data() {
		return {
			username:'123456',
			password:'123456'
		}
	},
	onLoad() {
		that = this;
	},
	methods: {
		login(){
			that.$utils.http(that.$api.login,{
				username:that.username,
				password:that.password
			},function(res){
				that.$store.setToken(res);
				
				uni.switchTab({
					url: '/pages/product/product'
				})
			})
		}
	}
}
